body {
  font-family: 'Open Sans', sans-serif;

  h1, h2, h3 {
    font-family: supria-sans-condensed, sans-serif;
    font-weight: 500;
    text-transform: uppercase;
  }

  .callout {
    font-family: "Roboto Mono", monospace;
    border: 1px solid black;
    padding: 2em;
  }

  strong {
    font-weight: 700;
  }

  div[class|=part] {
    padding-top: 3em;
    padding-bottom: 3em;
  }

  .container .col-lg-6 {
    padding: 0;


    @media (min-width: $medium-devices) {
      .container .col-lg-6 {
        padding: 0 15px;
      }
    }
  }
}

